@import url(https://fonts.googleapis.com/css2?family=Libre+Barcode+39+Text&display=swap);
* {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
}


@-webkit-keyframes expand {
  0%{
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100%{
   -webkit-transform: scale(1);
           transform: scale(1);
  }
}


@keyframes expand {
  0%{
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100%{
   -webkit-transform: scale(1);
           transform: scale(1);
  }
}


.gridProduct{
  grid-template-columns: repeat(4,1fr); 
}
@media (max-width: 1200px){
  .gridProduct{
    grid-template-columns: repeat(3,1fr);
  }
}
@media (max-width: 768px){
  .gridProduct{
    grid-template-columns: repeat(2,1fr);
  }
}

.container{
  -webkit-animation: expand 350ms ease-in-out;
          animation: expand 350ms ease-in-out;
}
.imgContainer{
  position: relative;
}

.imgContainer:hover .overlay {
  opacity: 1;

}

.overlay{
  position: absolute;
  bottom: 3px;
  background: rgb(255, 255, 255);
  background: rgba(255, 255, 255, 0.5); /* Black see-through */
  color: #333;
  width: 100%;
  height: 100%;
  transition: .5s ease-in-out;
  opacity:0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.detailsContainer {
  width: 100vw; 
  height: 90vh; 
  display: -webkit-flex; 
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  padding: 0em 2em;
  -webkit-animation: appear 1s ease    ;
          animation: appear 1s ease    ;
}

.closeBtnHolder {
  height: 15vh; 
  position: absolute;
  top: 10vh;
  right: 5vh;
}

.detailsPage {
  display: -webkit-flex;
  display: flex;
  margin-top: 6vh;
  height: 80vh;
}

.formContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100%;
  width: 70%;
  -webkit-justify-content: center;
          justify-content: center;
  margin-bottom: 10vh;
}

@-webkit-keyframes appear {
  0%{
    opacity: 0;
  }
  100%{
    opacit: 1;
  }
}

@keyframes appear {
  0%{
    opacity: 0;
  }
  100%{
    opacit: 1;
  }
}
@media (max-width: 768px){

  .detailsPage{
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .formContainer{
    width: 100%;
  }
}
.navbar{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  border-bottom: 1px solid #252525;
}
.brand-title {
  font-size: 2.2rem;
  margin: .8rem;
  font-family: 'Libre Barcode 39 Text', cursive;
}
.brand-title a {
  text-decoration: none;
  color: #1b1b1b
}
.navbar-links ul{
  margin: 0;
  padding: 0;
  display: -webkit-flex;
  display: flex;
}

.navbar-links li {
  list-style: none;
}
.navbar-links li a {
  text-decoration: none;
  color: #1b1b1b;
  padding: 1rem;
  display: block;

}
.cartBtn {
  margin-right: 1em;
  width: 170px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}
.navbar-links li:hover{

  opacity: .7;
}
.btnHolder{
  position: absolute;
  top: .3rem;
  right: 1rem;
  display: none;
}

@media (max-width: 768px){
  .btnHolder{
    display: -webkit-flex;
    display: flex;
  }

  .navbar-links{
    display: none;
    width: 100%;
  }
  .navbar{
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
  }
  .navbar-links ul {
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100%;
  }
  .navbar-links li {
    text-align: center;
  }
  .navbar-links li a {
    padding: .5rem 1rem;
  }
  .navbar-links.active{
    display: -webkit-flex;
    display: flex;

  }
  .cartBtn {
    display: none;
  }
}
